import React from 'react'
import { Table, Button } from 'antd';
import {
    PlusOutlined,
    SwapOutlined,
    DeleteOutlined,
    QrcodeOutlined
} from '@ant-design/icons';

const columns = [
  {
    title: 'Name',
    dataIndex: 'name',
  },
  {
    title: 'Age',
    dataIndex: 'age',
  },
  {
    title: 'Address',
    dataIndex: 'address',
  },
];

const data = [];
for (let i = 0; i < 46; i++) {
  data.push({
    key: i,
    name: `Edward King ${i}`,
    age: 32,
    address: `London, Park Lane no. ${i}`,
  });
}

class ListFrom extends React.Component {
  state = {
    selectedRowKeys: [], // Check here to configure the default column
    loading: false,
  };

  start = () => {
    this.setState({ loading: true });
    // ajax request after empty completing
    setTimeout(() => {
      this.setState({
        selectedRowKeys: [],
        loading: false,
      });
    }, 1000);
  };

  onSelectChange = selectedRowKeys => {
    console.log('selectedRowKeys changed: ', selectedRowKeys);
    this.setState({ selectedRowKeys });
  };

  render() {
    const { loading, selectedRowKeys } = this.state;
    const rowSelection = {
      selectedRowKeys,
      onChange: this.onSelectChange,
    };
    const hasSelected = selectedRowKeys.length > 0;
    return (
      <div>
        <div style={{ marginBottom: 16 ,marginTop:16}}>
            <Button type="primary" icon={<PlusOutlined />}>添加</Button>
            <Button type="primary" icon={<DeleteOutlined />} style={{marginLeft:20}}>批量删除</Button>
            <Button type="primary" icon={<SwapOutlined />} style={{marginLeft:20}}>排序</Button>
            <Button type="primary" icon={<QrcodeOutlined />} style={{marginLeft:20}}>导出设置二维码</Button>
        </div>
        <Table rowSelection={rowSelection} columns={columns} dataSource={data} />
      </div>
    );
  }
}

export default ListFrom;