import React, { Component } from "react";
import {
    Card,
    Menu,
    Dropdown,
    Button,
    Input,
    Table
} from 'antd'
import './index.css'
import {
    DownOutlined,
    createFromIconfontCN,
    PlusOutlined,
    MinusOutlined
} from '@ant-design/icons'


const columns = [
    {
        title: 'Name',
        dataIndex: 'name',
    },
    {
        title: 'Age',
        dataIndex: 'age',
    },
    {
        title: 'Address',
        dataIndex: 'address',
    },
];

const data = [];
for (let i = 0; i < 46; i++) {
    data.push({
        key: i,
        name: `Edward King ${i}`,
        age: 32,
        address: `London, Park Lane no. ${i}`,
    });
}

class AlarmNotice extends Component {
    state = {
        selectedRowKeys: [], // Check here to configure the default column
    }
    MenuView = () => {
        return (
            <Menu>
                <Menu.Item>
                    <a target="_blank" rel="noopener noreferrer" href="http://www.alipay.com/">
                        项目1
                    </a>
                </Menu.Item>
                <Menu.Item>
                    <a target="_blank" rel="noopener noreferrer" href="http://www.taobao.com/">
                        项目2
                    </a>
                </Menu.Item>
                <Menu.Item>
                    <a target="_blank" rel="noopener noreferrer" href="http://www.tmall.com/">
                        项目3
                    </a>
                </Menu.Item>
            </Menu>
        )
    }
    onSelectChange = selectedRowKeys => {
        console.log('selectedRowKeys changed: ', selectedRowKeys);
        this.setState({ selectedRowKeys });
    }
    render() {
        const { selectedRowKeys } = this.state;
        const rowSelection = {
            selectedRowKeys,
            onChange: this.onSelectChange,
        };
        return (
            <Card
                title="报警通知"
                bordered={false}
                style={{ width: '100%' }}
            >
                <div className="head">
                    <Dropdown overlay={this.MenuView()}>
                        <Button>
                            全部项目<DownOutlined />
                        </Button>
                    </Dropdown>
                    <Input.Search
                        placeholder="input search text"
                        onSearch={value => console.log(value)}
                        enterButton
                        style={{ width: 300, marginLeft: 6 }}
                    />
                    <Button
                        type="primary"
                        icon={<PlusOutlined />}
                        style={{ marginLeft: 50 }}
                    >添加</Button>
                    <Button
                        type="primary"
                        icon={<MinusOutlined />}
                        style={{ marginLeft: 6 }}
                    >批量删除</Button>
                </div>
                <Table 
                    rowSelection={rowSelection} 
                    columns={columns} 
                    dataSource={data}
                    style={{marginTop:26}}
                />
            </Card>
        )
    }
}

export default AlarmNotice;