class Auth {
    
    /**
     * @method 获取token
     */
    static authenticate() {
        const token = sessionStorage.getItem("loginToken");
        return !!token ? true : false;
    }
    /**
     * @method 设置token
     * @param { String } token 值
     */
    static setToken(token) {
        sessionStorage.setItem("loginToken", token);
        return true;
    }
    /**
     * @method 释放token
     */
    static signout() {
        sessionStorage.removeItem('loginToken');

    }

}

export default Auth;